@import '../common/styles/variable';

.aboutMe {
  background-color: $bgColorSecond;
  display: flex;
  .wrapperSkills {
    display: flex;
    gap: 10px;
    padding: 0;
    flex-wrap: wrap;
    @media(max-width: 940px){
      justify-content: space-around;
      @media(max-width: 735px){
        justify-content: center;
      }
    }
  }
}

